
#app {
  font-family: 'Pacifico', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #292222;
  margin-top: 40px;
  font-size: 1rem;
}
html {
    background-color: #292222;
}
header {
  color: #eeeeee;
}
footer {
  background-color: #292222;
  color: white;
}
button {
  color: #ffffff;
}
#main-header {
  font-size: 5rem;
  letterSpacing: 2px;
}
#pad {
  background-color: #ffffff;
  border: 4px solid #000000;
}
#tasks {
  font-size: 1.7rem;
}
#task-input {
  letter-spacing: 3px;
  font-size: 1.4rem;
}
.task:hover {
  background-color: #dddddd;
  cursor: pointer;
}
.delete:hover {
  color: #ee0000;
  cursor: pointer;
}
.line {
  text-decoration: line-through;
}
.completed {
  background-color: #009900;
  opacity: 0.5;
}
.completed:hover {
  background-color: #009900;
  opacity: 0.9;
}
.complete:hover {
  color: #0981f9;
  cursor: pointer;
}

